<template>
	<div class="bottom">
		<div class="d_items" @click="goto('home')"><img v-if='acIndex!==1' src="./1.png" alt=""><img v-if='acIndex==1' src="./1-ac.png" alt=""><span :class="acIndex==1?'ac':''"></span></div>
		<div class="d_items" @click="goto('focus')"><img v-if='acIndex!==2' src="./2.png" alt=""><img v-if='acIndex==2' src="./2-ac.png" alt=""><span :class="acIndex==2?'ac':''"></span></div>
		<div class="d_items" @click="goto('discover')"><img v-if='acIndex!==3' src="./3.png" alt=""><img v-if='acIndex==3' src="./3-ac.png" alt=""><span :class="acIndex==3?'ac':''"></span></div>
		<div class="d_items" @click="goto('message')"><img v-if='acIndex!==4' src="./4.png" alt=''><img v-if='acIndex==4' src="./4-ac.png" alt=""><span :class="acIndex==4?'ac':''"></span></div>
		<div class="d_items" @click="goto('center')"><img v-if='acIndex!==5' src="./5.png" alt=""><img v-if='acIndex==5' src="./5-ac.png" alt=""><span :class="acIndex==5?'ac':''"></span></div>
	</div>
</template>
<script>
	export default {
		props:{
			acIndex:{
				type:Number,
				default:1
			}
		},
		data(){
			return {

			}
		},
		methods:{
			goto(path){
				this.$router.push({name:path})
			}
		},
	}
</script>
<style lang="stylus" scoped>
	.bottom
		z-index 10000
		display flex
		width 100%;
		height 0.5rem
		padding 10px 0
		background #fff
		position fixed
		left 0 
		bottom 0
		.d_items
			flex 1
			font-size 0.16rem
			img
				display block
				margin 0 auto
				width 0.25rem
				height 0.25rem
				margin-bottom 0.1rem
			span.ac
				color #ff9645
		
</style>



